.layout-block {
  background-color: rgba(164, 164, 164, 0.38);
  padding: 0.5rem;
  border: 1px dashed #969696;
  position: relative; }
  .layout-block--nopadding {
    padding: 0; }
  .layout-block--margin-top {
    margin-top: 10px; }
  .layout-block--1x1 {
    flex: 1 0 auto; }
  .layout-block--1x1:after {
    content: "";
    display: block;
    padding-bottom: 100%; }
  .layout-block--4x3 {
    flex: 1 1 auto; }
  .layout-block--4x3:after {
    content: "";
    display: block;
    padding-bottom: 75%; }
  .layout-block--16x9 {
    flex: 1 1 auto; }
  .layout-block--16x9:after {
    content: "";
    display: block;
    padding-bottom: 56.25%; }
  .layout-block__content {
    padding: 0.5rem;
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0; }

