/* Components */
.wrapper {
  @apply max-w-3xl w-full m-auto flex; }

.sidebar {
  @apply grey-light-gradient w-32 hidden shiny-grey; }

@screen lg {
  .sidebar {
    @apply block; } }

.vertical-menu {
  @apply h-full flex-col justify-between hidden mt-10; }

@screen md {
  .vertical-menu {
    @apply flex; } }
  .vertical-menu__list {
    @apply list-reset; }
  .vertical-menu__link {
    @apply block border-solid border-r-4 border-white text-right flex-col flex p-8 items-center; }
  .vertical-menu__link-icon {
    @apply text-size--large; }
  .vertical-menu__link-text {
    @apply whitespace-no-wrap; }

.main-content {
  @apply flex-auto grey-gradient p-5; }

@screen md {
  .main-content {
    @apply p-10; } }

.header {
  @apply flex justify-between items-center mb-10; }

.data-group-media {
  @apply flex; }
  .data-group-media__content {
    @apply pl-4; }

.sidebar-toggle {
  @apply p-4; }

@screen md {
  .sidebar-toggle {
    @apply hidden; } }

.grid {
  @apply flex flex-wrap -m-2; }
  .grid__item {
    @apply p-2 w-full; }

@screen md {
  .grid__item {
    @apply w-1/3; } }

.panel {
  @apply rounded-sm shadow bg-white; }
  .panel + .panel {
    margin-top: 1rem; }
  .panel__header {
    @apply flex justify-between items-center p-4 flex-wrap; }

@screen md {
  .panel__header {
    @apply p-8; } }
  .panel__content {
    @apply p-4 pt-0 relative; }

@screen md {
  .panel__content {
    @apply p-8 pt-0; } }
    .panel__content canvas {
      max-width: 100%; }

.panel-table {
  @apply w-full; }
  .panel-table__row {
    @apply border-t; }
  .panel-table__cell {
    @apply py-4; }
  .panel-table__cell-flex {
    @apply flex justify-between; }

.footer {
  @apply flex flex-wrap mt-10; }
  .footer__column {
    @apply w-full; }

@screen xl {
  .footer__column {
    @apply w-3/5; } }
  .footer__heading {
    @apply mb-2 uppercase text-grey-dark; }
  .footer__list {
    @apply list-reset hidden; }

@screen sm {
  .footer__list {
    @apply block; } }
  .footer__list-item {
    @apply text-size--small text-grey-dark; }
  .footer__social-media-wrap {
    @apply flex; }
  .footer__social-media-icon {
    @apply rounded-full flex items-center justify-center p-4 border-grey-darker border-2; }

